export default {
  layout: "default",
  data() {
    return {
      loadMoreLoading: false,
      datas: [],
      seoObject: {},
      next: "",
      imgpath: "http://cdn.resultonlinebd.com/media/"
    };
  },
  head() {
    return {
      title: this.seoObject.tag_name,
      meta: [
        {
          hid: "description",
          name: "description",
          content: `Top articles of ${this.seoObject.tag_name}`
        },
        {
          hid: "keywords",
          name: "keywords",
          content: this.seoObject.tag_name
        },
        {
          hid: "twitter:card",
          name: "twitter:card",
          content: this.seoObject.tag_name
        },
        {
          hid: "twitter:title",
          name: "twitter:title",
          content: this.seoObject.tag_name
        },
        {
          hid: "twitter:description",
          name: "twitter:description",
          content: `Top articles of ${this.seoObject.tag_name}`
        },
        {
          hid: "og:title",
          property: "og:title",
          content: this.seoObject.tag_name
        },
        {
          hid: "og:type",
          property: "og:type",
          content: "article.text"
        },
        {
          hid: "og:url",
          property: "og:url",
          content: ""
        },
        {
          hid: "og:description",
          name: "og:description",
          content: `Top articles of ${this.seoObject.tag_name}`
        }
        // {
        //   hid: "og:image",
        //   name: "og:image",
        //   content: this.seoObject.meta_image
        // }
      ]
    };
  },

  async fetch() {
    var self = this;
    await self.$axios
      .$get(process.env.baseUrl + `/tagPage/${self.$route.params.tag}`)
      .then(function(posts) {
        self.datas = posts.results;
        self.next = posts.next;
        self.seoObject = posts.results;
      })
      .catch(function(e) {
        console.log(e);
      });
  },
  methods: {
    async setview(view, slug) {
      // try {
      //   await this.$axios.$put(process.env.baseUrl + `/count/${slug}`, {
      //     view: view + 1
      //   });
      // } catch (e) {
      //   console.log(e);
      // }
    },
    async loadData() {
      this.loadMoreLoading = true;
      if (this.next != null) {
        var self = this;
        await self.$axios
          .$get(self.next)
          .then(function(posts) {
            posts.results.List.forEach(element => {
              self.datas.List.push(element);
            });
            self.next = posts.next;
          })
          .catch(function(error) {
            console.log("No Net" + error);
          })
          .finally(function() {});
      } else {
        // alert("Null");
      }
      this.loadMoreLoading = false;
    }
  }
};
